import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Spin } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import {
  getStudentForProject,
  getStudentForGraduateTest
} from 'services/studentQualifyForGraduate';
import { makeid } from 'utils/functionUtil';
import TabInfoStudentPreparingGraduation from './TabInfoStudentPreparingGraduation';
import FormSearch from './FormSearch';
import './style.scss';

class ListStudentPreparingToGraduationPage extends Component {
  state = {
    loading: false,
    paramSearch: {},
    dataStudentForProject: [],
    dataStudentForGraduateTest: [],
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    const paramSearch = getQueryParams();
    this.setState({
      paramSearch,
    },
      () => this.handleSearch(paramSearch),
    );
  };

  handleSearch = async (paramSearch) => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    if (
      !!paramSearch.trainTypeId &&
      !!paramSearch.facultyId &&
      !!paramSearch.courseId &&
      !!paramSearch.studyFieldId
    ) {
      this.setState({
        loading: true,
      });
      await Promise.all([
        getStudentForProject(paramSearch),
        getStudentForGraduateTest(paramSearch),
      ])
        .then((responses) => {
          const tempDataForProject = responses[0].data.items.map(item => {
            item.autoKey = `${makeid(8)}_${item.id}`;
            return item;
          })
          const tempDataForGraduateTest = responses[1].data.items.map(item => {
            item.autoKey = `${makeid(8)}_${item.id}`;
            return item;
          })
          this.setState({
            loading: false,
            dataStudentForProject: [...tempDataForProject],
            dataStudentForGraduateTest: [...tempDataForGraduateTest],
          });
        })
        .catch((error) => {
          console.log(error);
          this.setState({
            loading: false,
          });
        });
    }
  }

  render() {
    const { 
      loading, 
      dataStudentForProject, 
      dataStudentForGraduateTest,
      paramSearch
    } = this.state;
    return (
      <Spin spinning={loading}>
        <Card title="Xét điều kiện khóa luận, thi tốt nghiệp">
          <Row className="rowMargin">
            <Col span={24}>
              <FormSearch />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TabInfoStudentPreparingGraduation
                dataStudentForProject={dataStudentForProject}
                dataStudentForGraduateTest={dataStudentForGraduateTest}
                onJoinGraduateTestSuccess={this.handleChangeQueryParams}
                queryParams={paramSearch}
              />
            </Col>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStudentPreparingToGraduationPage />
  </Page>
);



