import React, { Component } from 'react';
import { Row, Form } from 'antd';
import { BtnSave, BtnExport } from 'components/common/button';
import { TableForm } from 'components/common';

@Form.create({ name: 'formTableQualifiedDoThesis' })
class TableQualifiedDoThesis extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {};
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  columns = [
    {
      title: 'Mã sinh viên',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'classname',
      key: 'classname',
    },
    {
      title: 'TBC tích lũy',
      dataIndex: 'totalGrade',
      key: 'totalGrade',
    },

    {
      title: 'Xếp hạng',
      dataIndex: 'classification',
      key: 'classification',
    },
  ];

  render() {
    const {
      reload,
      rowKey,
      data,
      handleSelectedRows,
      form,
      joinGraduateTest,
      exportPDF,
      exportExcel,
      disableJoinBtn,
      disableExport
    } = this.props;
    return (
      <div>
        <Row>
          <div className="btn-group" style={{ float: 'right' }}>
            <BtnSave title="Thi tốt nghiệp" onClick={joinGraduateTest} disabled={disableJoinBtn} />
            <BtnExport title="Xuất Excel" onClick={exportExcel} disabled={disableExport} />
            <BtnExport title="Xuất PDF" onClick={exportPDF} disabled={disableExport} />
          </div>
        </Row>
        <TableForm
          reload={reload}
          data={data}
          handleSelectedRows={handleSelectedRows}
          rowKey={rowKey}
          columns={this.columns}
          form={form}
          ref={this.refTable}
        />
      </div>
    );
  }
}

export default TableQualifiedDoThesis;



