import React, { Component } from 'react';
import { Row, Col, Tabs, Spin, message } from 'antd';
import {
  joinGraduateTest,
  exportExcelStudentForProject,
  exportPdfStudentForProject,
  exportExcelStudentForGraduateTest,
  exportPdfStudentForGraduateTest,
} from 'services/studentQualifyForGraduate';
import {
  getListDataValid
} from 'utils/formUtil';
import TableQualifiedDoThesis from './TableQualifiedDoThesis';
import TableQualifiedExamGraduation from './TableQualifiedExamGraduation';
import './style.scss';

class TabInfoStudentPreparingGraduation extends Component {
  constructor(props) {
    super(props);
    this.refFormForProject = React.createRef();
    this.refFormRawForProject = React.createRef();
    this.refFormForGraduateTest = React.createRef();
    this.refFormRawForGraduateTest = React.createRef();
    this.state = {
      loading: false,
      reloadForProject: 0,
      reloadForGraduateTest: 0,
      selectedRows: [],
      rowKey: "autoKey",
    };
  }

  handleSelectedRows = (selectedRows) => {
    this.setState({
      selectedRows,
    });
  }

  joinGraduateTest = () => {
    try {
      const { selectedRows, rowKey } = this.state;
      const { onJoinGraduateTestSuccess, queryParams } = this.props;
      if (selectedRows && selectedRows.length) {
        const listFieldName = [];
        this.setState({
          loading: true,
        });
        getListDataValid(rowKey, listFieldName, this.refFormForProject, this.refFormRawForProject, selectedRows)
          .then(
            async (listSelectedItem) => {
              const studentIds = listSelectedItem.map(item => {
                return item.studentId
              });
              const formData = {
                ...queryParams,
                studentIds
              }

              try {
                await joinGraduateTest(formData);
                message.success('Thi tốt nghiệp thành công');
                this.setState({
                  selectedRows: [],
                  loading: false,
                },
                  () => onJoinGraduateTestSuccess(),
                );
              } catch (error) {
                console.log(error);
              } finally {
                this.setState({
                  loading: false,
                });
              }
            }
          )
          .catch(
            () => {
              this.setState({
                loading: false,
              });
            }
          )
      }
    } catch (error) {
      console.log(error);
    }
  }

  handleExportPDFForProject = async () => {
    try {
      this.setState({
        loading: true,
      });
      const { queryParams } = this.props;
      await exportPdfStudentForProject(queryParams);

    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcelForProject = async () => {
    try {
      this.setState({
        loading: true,
      });
      const { queryParams } = this.props;
      await exportExcelStudentForProject(queryParams);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportPDFForGraduateTest = async () => {
    try {
      this.setState({
        loading: true,
      });
      const { queryParams } = this.props;
      await exportPdfStudentForGraduateTest(queryParams);

    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcelForGraduateTest = async () => {
    try {
      this.setState({
        loading: true,
      });
      const { queryParams } = this.props;
      await exportExcelStudentForGraduateTest(queryParams);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  render() {
    const { TabPane } = Tabs;
    const {
      dataStudentForProject,
      dataStudentForGraduateTest,
      queryParams
    } = this.props;
    const {
      reloadForProject,
      reloadForGraduateTest,
      rowKey,
      loading,
      selectedRows,
    } = this.state;
    const disableJoinBtn = !(selectedRows && selectedRows.length);
    const disableExport = !(!!queryParams.trainTypeId && !!queryParams.facultyId && !!queryParams.courseId && !!queryParams.studyFieldId);
    return (
      <Spin spinning={loading}>
        <Row>
          <Col span={24}>
            <Tabs defaultActiveKey="1">
              <TabPane tab="Khóa luận" key="1">
                <TableQualifiedDoThesis
                  reload={reloadForProject}
                  data={dataStudentForProject}
                  ref={this.refFormForProject}
                  handleSelectedRows={this.handleSelectedRows}
                  rowKey={rowKey}
                  wrappedComponentRef={(form) => {
                    this.refFormRawForProject = form;
                  }}
                  joinGraduateTest={this.joinGraduateTest}
                  exportPDF={this.handleExportPDFForProject}
                  exportExcel={this.handleExportExcelForProject}
                  disableJoinBtn={disableJoinBtn}
                  disableExport={disableExport}
                />
              </TabPane>
              <TabPane tab="Thi tốt nghiệp" key="2">
                <TableQualifiedExamGraduation
                  reload={reloadForGraduateTest}
                  data={dataStudentForGraduateTest}
                  ref={this.refFormForGraduateTest}
                  rowKey={rowKey}
                  wrappedComponentRef={(form) => {
                    this.refFormRawForGraduateTest = form;
                  }}
                  exportPDF={this.handleExportPDFForGraduateTest}
                  exportExcel={this.handleExportExcelForGraduateTest}
                  disableExport={disableExport}
                />
              </TabPane>
            </Tabs>
          </Col>
        </Row>
      </Spin>
    );
  }
}

export default TabInfoStudentPreparingGraduation;



