import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const getStudentForProject = (
  {
    trainTypeId,
    facultyId,
    studyFieldId,
    courseId,
    classId,
    grade,
    minimumNoOfCredit,
    noOfCreditRetest,
    page,
    pageSize = 10,
    orderBy,
    isPagingEnabled = true,
  }
) =>
  request({
    prefix,
    url: `/StudentQualifyForGraduateTest/GetStudentForProject`,
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      studyFieldId,
      courseId,
      classId,
      grade,
      minimumNoOfCredit,
      noOfCreditRetest,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getStudentForGraduateTest = (
  {
    trainTypeId,
    facultyId,
    studyFieldId,
    courseId,
    classId,
    page,
    pageSize = 10,
    orderBy,
    isPagingEnabled = true,
  }
) =>
  request({
    prefix,
    url: `/StudentQualifyForGraduateTest/GetStudentForGraduateTest`,
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      studyFieldId,
      courseId,
      classId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const joinGraduateTest = ({
  userLogged,
  trainTypeId,
  facultyId,
  studyFieldId,
  courseId,
  classId,
  grade,
  minimumNoOfCredit,
  noOfCreditRetest,
  studentIds
}) =>
  request({
    prefix,
    url: `/StudentQualifyForGraduateTest/JoinGraduateTest`,
    data: {
      userLogged,
      trainTypeId,
      facultyId,
      studyFieldId,
      courseId,
      classId,
      grade,
      minimumNoOfCredit,
      noOfCreditRetest,
      studentIds
    },
    method: 'POST',
  });

export const exportExcelStudentForProject = ({
  trainTypeId,
  facultyId,
  studyFieldId,
  courseId,
  classId,
  grade,
  minimumNoOfCredit,
  noOfCreditRetest,
}) =>
  requestDownload({
    prefix,
    defaultFileName: `StudentForProject.excel`,
    url: `/StudentQualifyForGraduateTest/ExportExcelStudentForProject`,
    method: 'POST',
    data: {
      trainTypeId,
      facultyId,
      studyFieldId,
      courseId,
      classId,
      grade,
      minimumNoOfCredit,
      noOfCreditRetest,
    },
  });

export const exportPdfStudentForProject = ({
  trainTypeId,
  facultyId,
  studyFieldId,
  courseId,
  classId,
  grade,
  minimumNoOfCredit,
  noOfCreditRetest,
}) =>
  requestDownload({
    prefix,
    defaultFileName: `StudentForProject.pdf`,
    url: `/StudentQualifyForGraduateTest/ExportPdfStudentForProject`,
    method: 'POST',
    data: {
      trainTypeId,
      facultyId,
      studyFieldId,
      courseId,
      classId,
      grade,
      minimumNoOfCredit,
      noOfCreditRetest,
    },
  });

export const exportExcelStudentForGraduateTest = ({
  trainTypeId,
  facultyId,
  studyFieldId,
  courseId,
  classId,
}) =>
  requestDownload({
    prefix,
    defaultFileName: `StudentForGraduateTest.excel`,
    url: `/StudentQualifyForGraduateTest/ExportExcelStudentForGraduateTest`,
    method: 'POST',
    data: {
      trainTypeId,
      facultyId,
      studyFieldId,
      courseId,
      classId,
    },
  });

export const exportPdfStudentForGraduateTest = ({
  trainTypeId,
  facultyId,
  studyFieldId,
  courseId,
  classId,
}) =>
  requestDownload({
    prefix,
    defaultFileName: `StudentForGraduateTest.pdf`,
    url: `/StudentQualifyForGraduateTest/ExportPdfStudentForGraduateTest`,
    method: 'POST',
    data: {
      trainTypeId,
      facultyId,
      studyFieldId,
      courseId,
      classId,
    },
  });



