import React, { Component } from 'react';
import { Col, Form, Input, Row } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormFilterReward extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        </Row>
        <Row gutter={24}>
          <Col span={7}>
            <Form.Item label="Điều kiện TBCHT tích lũy >= ">
              {getFieldDecorator('grade', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={7}>
            <Form.Item label="Số tín chỉ tối thiểu đạt >= ">
              {getFieldDecorator('minimumNoOfCredit', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={7}>
            <Form.Item label="Số tín chỉ thi lại <= ">
              {getFieldDecorator('noOfCreditRetest', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col>
            <div className="btn-group" style={{ float: 'right' }}>
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormFilterReward;



